.campo{
    margin: 24px 0 ;
    font-family: 'Montserrat', sans-serif;
}

.campo label{
  font-family: 'Montserrat', sans-serif; 
  font-size: 18px;
  font-weight:600; 
  display:block;
  margin-bottom: 8px;
}

.campo input{
width: 100%;
background-color: #FFFFFF;
box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
border: none;
padding: 30px 20px;
outline-color:#FFA86F ;
font-size: 16px;
box-sizing: border-box;
}

.campo-color input{
height: 40px;
padding: 5px 20px;

}